@font-face {
  font-family: "tusker";
  src: url(./fonts/tusker-grotesk-font/TuskerGrotesk-2600Semibold.ttf);
}
@font-face {
  font-family: "migra";
  src: url(./fonts/migra/migrabold.otf);
}

* {
  margin: 0;
  padding: 0;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}
body {
  background-color: rgb(28, 28, 28);
}

ul {
  list-style: none;
}

.main-container {
  margin: 0 25px;
}

a {
  color: white;
  text-decoration: none;
}
