.nav-container {
  position: sticky;
  top: 0px;
  width: 100vw;
  z-index: 3;
  transform: translateY(-100%);
  transition: 0.8s ease-out;
  transition-delay: 1s;
  opacity: 0;
}
@media screen and (max-width: 500px) {
  .nav-container {
    max-width: 490px;
  }
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2% 0;
  border-bottom: 1px solid rgb(255, 255, 255);
  width: 92%;
  margin: 0 4%;
  margin-bottom: 10%;
}
nav .right ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 500px) {
  nav .right ul {
    display: none;
  }
}
