  display: flex;
  justify-content: center;
  align-items: center;
}
nav .left p {
  padding-right: 20px;
}
nav .left p a {
  font-family: "migra";
}
nav .relative-strike {
  position: relative;
}
nav .strike {
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: white;
  top: 50%;
  transition: 2s;
  animation: strike-reveal 0.5s forwards ease-out;
}
@keyframes strike-reveal {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
nav i {
  font-size: 20px;
}
@media screen and (min-width: 500px) {
  nav .burger {
    display: none;
  }
}
