.seeothercases {
    
    overflow: hidden;
    position: relative;
    border: 1px solid rgb(255, 255, 255);
    padding: 8px 12px;
    border-radius: 300px;
    margin: 10px;
    background-color:rgb(222, 217, 208) ;
   
  }

  .seeothercases span {
    white-space: nowrap;
    display: inline-block;
    a {
      text-decoration: none;
      color: black;
      font-size: 27px;
      @media screen and ( max-width:1080px) {
        font-size: 16px;
      }
    }
  }
  .seeothercases span::before {
    content: "SEE OTHER CASES";
    position: absolute;
    font-size: 27px;
    transform: translateX(110%);
    overflow: hidden;
    color: black;

    @media screen and ( max-width:1080px) {
      font-size: 16px;
    }
  }

  .seeothercases:hover span {
    animation: scroll 1.2s linear infinite;
  }

  @keyframes scroll {
    0% {
      transform: translateX(0%);
    }
    100% {
      transform: translateX(-110%);
    }
  }