button {
    
    overflow: hidden;
    position: relative;
    border: 1px solid rgb(255, 255, 255);
    padding: 8px 12px;
    border-radius: 300px;
    margin: 10px;
    background-color: rgba(245, 245, 245, 0);
   
  }

  button span {
    white-space: nowrap;
    display: inline-block;
    a {
      text-decoration: none;
      color: white;
      font-size: 27px;
      @media screen and ( max-width:1080px) {
        font-size: 16px;
      }
    }
  }
  button span::before {
    content: "CONTACT ME";
    position: absolute;
    font-size: 27px;
    transform: translateX(110%);
    overflow: hidden;
    @media screen and ( max-width:1080px) {
      font-size: 16px;
    }

  }

  button:hover span {
    animation: scroll 1.2s linear infinite;
  }

  @keyframes scroll {
    0% {
      transform: translateX(0%);
    }
    100% {
      transform: translateX(-110%);
    }
  }