.footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    font-size: 20px;
    margin-bottom: 20px;
    @media screen and (max-width:500px) {
        flex-direction: column;
        align-items: flex-start;
    }

   

    .left {
        display: flex;
        justify-content: center;
        align-items: center;

        p {
            margin-right: 15px;
            @media screen and (max-width:500px) {
                font-size: 10px;
            }
        }
    }
    .right {
        display: flex;
        justify-content: center;
        align-items: center;

        p {
            margin-left: 15px;
            @media screen and (max-width:500px) {
                font-size: 10px;
                margin-left: 0;
                margin-right: 15px;
            }
        }
    }
}