.portfolio {
    margin: 250px auto 0 ;
    max-width: 1117px;
    @media screen and (max-width:500px) {
       margin-top: 100px;
    
    }
    
.flex {
display: flex;
align-items: center;
margin: 30px 0;
height: 266px;

@media screen and (max-width:500px) {
    flex-direction: column;
    align-items: flex-end;
    height: 100%;

}

.title {
    width: 50%;
    @media screen and (max-width:500px) {
      width: 100%;
    }
  
}


    h3{
        font-family: 'tusker';
        font-size:100px;
        color: #f5eee6;
        line-height: 100px;
        @media screen and (max-width:500px) {
            font-size: 70px;
        }
            }
        
        
            .img {
                width: 50%;
                position: relative;
                transition: 0.7s ease-out;
                margin: 0 auto;
                overflow: hidden;
                @media screen and (max-width:500px) {
                    margin: 10px 0;
                    width: 100%;
                }
              
                &:hover {
                    width: 47%;
                    img{
                        transform: scale(1.3);
                    } 
                    @media screen and (max-width:500px) {
                        width: 100%
                       }
                }
                img {
                    width: 100%;
                    transition: 0.7s ease-out;
                    
                    
                }
                .background-img {
                    background-color: rgba(86, 73, 55, 0.364);
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    backdrop-filter:saturate(0%);
                    top: 0;
                    left: 0;
                }
            }
}
