.background {
  width: 100vw;
  height: 150vh;
  background-image: url("./peinture-romaine.jpg");
  background-position: bottom;
  background-size: cover;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;

  clip-path: polygon(50% 30%, 60% 50%, 50% 70%, 40% 50%);
  // transition: 1s cubic-bezier(0.57, 0.86, 0.63, 1.01);
  display: none;
}

.background-black {
  width: 100vw;
  height: 500vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.background-end {
  width: 100vw;
  height: 300vh;
  position: absolute;
  top: 500vh;
  left: 0;
  z-index: -1;
  background-color: rgb(23, 23, 23);
  @media screen and (max-width: 500px) {
    height: 250vh;
  }
}
