.header {
  display: flex;
  align-items: flex-end;
  margin: 0 auto 0;
  max-width: 1117px;
  @media screen and (max-width: 500px) {
    margin-top: 35px;
    align-items: flex-start;
  }

  .flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    @media screen and (max-width: 1080px) {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
    }

    .left {
      @media screen and (max-width: 1080px) {
        width: 100%;
      }

      p {
        color: #f7e3c7;
        font-family: "migra";
      }
    }

    p {
      margin-bottom: 5px;
    }
  }
  h1 {
    font-family: "tusker";
    line-height: 1em;
    font-size: 218px;
    font-weight: 100;
    padding: 0;
    margin: 0;
    color: #fbf3e9;

    @media screen and (max-width: 500px) {
      font-size: 100px;
      margin-bottom: 50px;
    }

    span {
      font-size: 30px;
      margin: 0;
      padding: 0;
      line-height: 1em;
      color: #f7e3c7;
      font-family: "migra";
    }
  }
  .text {
    max-width: 556px;
    margin: 0 auto;

    p {
      font-size: 28px;
      line-height: 40px;
      margin-bottom: 30px;
      @media screen and (max-width: 500px) {
        font-size: 15px;
        line-height: 20px;
      }
    }
    .first-line {
      text-align: end;
      margin: 0;
    }
  }

  .right {
    @media screen and (max-width: 1080px) {
      width: 100%;
    }
    .date {
      width: 100px;
    }
  }
}
