.nav-container {
  position: sticky;
  top: 0px;
  width: 100vw;
  z-index: 3;
  transform: translateY(-100%);
  transition: 0.8s ease-out;
  transition-delay: 1s;
  opacity: 0;

  @media screen and (max-width: 500px) {
    max-width: 490px;
  }
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2% 0;
  border-bottom: 1px solid rgb(255, 255, 255);
  width: 92%;
  margin: 0 4%;
  margin-bottom: 10%;

  .right ul {
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 500px) {
      display: none;
    }

    li {
      padding-left: 10px;
    }
  }
  .left {
    display: flex;
    justify-content: center;
    align-items: center;
    p {
      padding-right: 20px;

      a {
        font-family: "migra";
      }
    }
  }

  .relative-strike {
    position: relative;
  }
  .strike {
    position: absolute;
    width: 0%;
    height: 2px;
    background-color: white;
    top: 50%;
    transition: 2s;
    animation: strike-reveal 0.5s forwards ease-out;
  }
  @keyframes strike-reveal {
    from {
      width: 0%;
    }
    to {
      width: 100%;
    }
  }

  i {
    font-size: 20px;
  }
  .burger {
    @media screen and (min-width: 500px) {
      display: none;
    }
  }
  .bar {
    height: 2px;
    background-color: rgb(255, 255, 255);
    position: fixed;
    bottom: 0px;
  }
}
